const API_URL = 'https://api.github.com'
const usersPreview = 10;
const reposUser = 100;

const request = (url) => {
  return fetch(
    `${API_URL}${url}`,
    {
      headers: {
        "User-Agent": "search_github_users",
        Accept: "application/vnd.github.v3+json",
        authorization: "token 3f8c394317bae6e49bea00d52b8231b3204fa682",
      }
    })
    .then(response => {
      if (response.ok) {

        return response.json();
      }

      throw new Error(`${response.status} - ${response.statusText}`)
    })
}

export const getUsers = () => request(
  `/users?per_page=${usersPreview}`)

export const getUserRepos = (login) => request(
  `/users/${login}/repos?per_page=${reposUser}`)

export const getQueryUsers = (sbr) => (
  request(`/search/users?q=${sbr}`)
    .then(users => users.items)
)

export const getUserFollowers = (login) => (
  request(`/users/${login}/followers?per_page=${reposUser}`)
)

export const getUserFollowing = (login) => (
  request(`/users/${login}/following?per_page=${reposUser}`)
)
