import React, { useState } from 'react';
import './App.scss';
import { Home } from './components/Home';
import { UserPage } from './components/UserPage';

const App = () => {
  const [tab, setTab] = useState('home');
  const [userLogin, setUserLogin] = useState('');
  const [userId, setUserId] = useState(0);
  const [userAvatar, setUserAvatar] = useState('');
  const [userRepos, setUserRepos] = useState([]);
  const [placeholder, setPlaceholder] = useState('Search for Users');

  const addUserRepos = (userRepos) => {
    setUserRepos(userRepos);
  }

  const selectPage = (login, id, avatar_url) => {
    setTab('user_page');
    setUserAvatar(avatar_url);
    setUserId(id);
    setUserLogin(login);
  }

  const changePlaceholder = (placeholder) => (
    setPlaceholder(placeholder)
  )

  return (
    <div className="container">
      <h1
        className="container__h1"
        onClick={() => (
          setTab('home')
        )}
      >
        GitHub Searcher
      </h1>
      {tab === 'home' &&
      <Home
        selectPage={selectPage}
        setUserRepos={addUserRepos}
        placeholder={placeholder}
        changePlaceholder={changePlaceholder}
      />}
      {tab !== 'home' &&
      <UserPage
        userRepos={userRepos}
        userLogin={userLogin}
        userId={userId}
        userAvatar={userAvatar}
        placeholder={placeholder}
        changePlaceholder={changePlaceholder}
      />
      }
    </div>
  );
}

export default App;
