import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import ReactTooltip from 'react-tooltip';

import { getUserRepos } from "../../api";
import { Loader } from '../Loader';

import './User.scss';

const maxLoginLength = 13;

export const User = ({
  login,
  id,
  avatar_url,
  selectPage,
  setUserRepos,
}) => {
  const [repos, setRepos] = useState('');
  const [prepareLogin, setPrepareLogin] = useState('');
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    let isMounted = true;

    if (login.length > maxLoginLength) {
      setPrepareLogin(`${login.slice(0, maxLoginLength)}...`);
    } else {
      setPrepareLogin(login);
    }

    getUserRepos(login)
      .then(userRepos => {
        if (isMounted) {
          setRepos(userRepos);
        }
      })

      return () => {isMounted = false};
  }, [login]);

  useEffect(() => {
    if (repos === '') {
      setLoading(true)
    } else (
      setLoading(false)
    )
  }, [repos]);

  return (
    <div
      key={id}
      className="userCard"
      onClick={() => {
        selectPage(login, id, avatar_url);
        setUserRepos(repos);
      }}
    >
      {!loading ? (
        <>
          <div className="userCard__item-wrap">
            <img
              className="userCard__avatar"
              src={avatar_url}
              alt='User avatar'
            />
            <p
              className="userCard__login"
              data-tip={login}
              data-type="info"
            >
              {prepareLogin}
            </p>
            <ReactTooltip />
          </div>
          <div
            className="userCard__repo"
          >
            <p>Repo:</p>
            <p
              className="userCard__value-repo"
            >
              {repos.length === 100
                ? `${repos.length}+`
                : repos.length}
            </p>
          </div>
        </>
      ) : (
        <Loader />
      )}
    </div>
  )
}

User.propTypes = {
  login: PropTypes.string.isRequired,
  id: PropTypes.number.isRequired,
  avatar_url: PropTypes.string.isRequired,
  selectPage: PropTypes.func.isRequired,
  setUserRepos: PropTypes.func.isRequired,
}
