import React, { useState, useEffect } from "react";
import PropTypes from 'prop-types';

import ReactTooltip from 'react-tooltip';

import { getUserFollowers, getUserFollowing } from "../../api";
import { SearchBar } from "../SearchBar";
import { RepoList } from '../RepoList';
import { Loader } from '../Loader';

import './UserPage.scss';

export const UserPage = ({
  userLogin,
  userAvatar,
  userRepos,
  placeholder,
  changePlaceholder,
}) => {
  const [userFollowers, setUserFollowers] = useState('');
  const [userFollowing, setUserFollowing] = useState('');
  const [preparedUserRepos, setPreparedUserRepos] = useState([]);
  const [searchQuery, setSearchQuery] = useState('');
  const [error, setError] = useState(false);
  const [prepareLogin, setPrepareLogin] = useState('');

  const handleQuery = (event) => {
    const {value} = event.target;

    setSearchQuery(value);
  }

  useEffect(() => (
    changePlaceholder('Search for Users Repositories')
  ), [changePlaceholder]);

  useEffect(() => {
    let isMounted = true;

    if (isMounted) {

      getUserFollowers(userLogin)
        .then(userFollowers =>
          setUserFollowers(userFollowers.length))

      getUserFollowing(userLogin)
        .then(userFollowing =>
          setUserFollowing(userFollowing.length))
    }

    if (userLogin.length > 13) {
      setPrepareLogin(`${userLogin.slice(0, 13)}...`)
    } else {
      setPrepareLogin(userLogin)
    }

    return () => {isMounted = false};
  }, [userLogin]);

  useEffect(() => {
    if (searchQuery) {
      setPreparedUserRepos(
        userRepos.filter(
          repo => repo.name
            .toLowerCase()
            .includes(searchQuery.toLowerCase()))
      );
    }

    if (!searchQuery) {
      setPreparedUserRepos(userRepos)
    }
  }, [userRepos, searchQuery]);

  useEffect(() => {
    if (preparedUserRepos.length === 0) {
      setError(true)
    } else {
      setError(false)
    }
  }, [preparedUserRepos]);


  return(
    userFollowers === '' ? (
      <div className="userCard__loader">
        <Loader />
      </div>
    ) : (
      <div className="user-page">
        <div className="user-page__info info">
          <img
            className="info__avatar"
            src={userAvatar}
            alt="Avatar"
          />
          <div className="info__items">
            <p
              className="info__items__login"
              data-tip={userLogin}
              data-type="info"
            >
              {prepareLogin}
            </p>
            <ReactTooltip />

            <p className="info__items__followers">
              Followers:
              {' '}
              <span className="info__items__followers-value">
              {userFollowers === 100
                ? `${userFollowers}+` : userFollowers}
            </span>
            </p>

            <p className="info__items__following">
              Following:
              {' '}
              <span className="info__items__following-value">
              {userFollowing === 100
                ? `${userFollowing}+` : userFollowing}
            </span>
            </p>

            <p className="info__items__repo">
              Repo value:
              {' '}
              <span className="info__items__repo-value">
              {userRepos.length}
            </span>
            </p>

          </div>
        </div>

        <div className="user-page__search-bar">
          <SearchBar
            placeholder={placeholder}
            getSearchQuery={handleQuery}
          />
        </div>

        {error ? (
          <p className="user-page__error">
            No matches found
          </p>
        ) : (
          <RepoList
            userLogin={userLogin}
            repos={preparedUserRepos}
          />
        )}
      </div>
    )
  )
}

UserPage.propTypes = {
  userLogin: PropTypes.string.isRequired,
  userAvatar: PropTypes.string.isRequired,
  placeholder: PropTypes.string.isRequired,
  changePlaceholder: PropTypes.func.isRequired,
  userRepos: PropTypes.array.isRequired,
}
